sabled],\n     &.disabled{\n        @include opacity(.5);\n        pointer-events: none;\n    }\n}\n.btn-simple{\n    border: $border;\n    border-color: $default;\n    box-shadow: none;\n    padding: $padding-btn-vertical - 1 $padding-round-horizontal - 1;\n    background-color: $transparent-bg;\n}\n\n.btn-simple,\n.btn-link{\n    &.disabled,\n    &:disabled,\n    &[disabled],\n    fieldset[disabled] & {\n        &,\n        &:hover,\n        &:focus,\n        &.focus,\n        &:active,\n        &.active {\n            background: $transparent-bg;\n        }\n    }\n}\n\n.btn:not(:disabled):not(.disabled):active, .btn:not(:disabled):not(.disabled).active{\n  box-shadow: 2px 2px 6px rgba(0,0,0,.4);\n}\n.btn-link{\n    border: $none;\n    box-shadow: none;\n    padding: $padding-base-vertical $padding-base-horizontal;\n    background: $transparent-bg;\n    color: $gray-300;\n    font-weight: $font-weight-bold;\n\n  &:hover {\n    box-shadow: none !important;\n    transform: none !important;\n  }\n}\n\n.btn-lg{\n   @include btn-size($btn-padding-y-lg, $btn-padding-x-lg, $font-size-base, $border-radius-lg);\n}\n.btn-sm{\n    @include btn-size($btn-padding-y-sm, $btn-padding-x-sm, $font-size-base, $border-radius-sm);\n}\n\n.btn-wd {\n    min-width: 140px;\n}\n.btn-group.select{\n    width: 100%;\n}\n\n.btn-group {\n  .btn.active {\n    box-shadow: 2px 2px 6px rgba(0,0,0,.4);\n    transform: translateY(-1px);\n    -webkit-transform: translateY(-1px);\n  }\n}\n\n\n.btn-group.select .btn{\n    text-align: left;\n}\n.btn-group.select .caret{\n    position: absolute;\n    top: 50%;\n    margin-top: -1px;\n    right: 8px;\n}\n\n.btn-group .btn.active {\n    box-shadow: 2px 2px 6px rgba(0,0,0,.4);\n    transform: translateY(-1px);\n    -webkit-transform: translateY(-1px);\n}\n\n.btn-round{\n    border-width: $border-thin;\n    border-radius: $btn-round-radius;\n\n    &.btn-simple{\n        padding: $padding-btn-vertical - 1  $padding-round-horizontal - 1;\n    }\n}\n\n.no-car